.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
.download {
  text-decoration: none;
  background: blue;
  color:#fff;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 15px;
  margin: 30px auto;
  max-width: 200px;
  padding: 1rem 2rem;
}
.settingns-wrapper {
  display: flex;
}
.setting-item {
  margin: 0 15px;
}
button {
  background: white;
  display: inline-block;
  padding: 5px 15px;
  border: none;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
